/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useRef } from "react"; 
import Typed from 'typed.js';


export default function Home() {


    const el = useRef(null);

  useEffect(() => {



    const typed = new Typed(el.current, {
        strings: ['Front-End developer!', 'Back-End Developer'],
        typeSpeed: 50,
        backSpeed: 70,
        loop: true,
        loopCount: Infinity,

    });

    return () => {
        typed.destroy();
    };
}, []);




return <>


<div id='home'>

<div className='container-fluid d-flex align-items-center vh-100' id='home'>
    <div className="container">
    <div className="row align-items-center">

            <div className="col-md-12" >
                <div className="descrption">
                    <h2 className='fw-bold fs-5 py-5' data-aos="fade-down" data-aos-duration="3000">Hello It's me</h2>
                    <h1 data-aos="fade-up" data-aos-duration="3000" className='fw-bolder fs-1'  >Ahmed Hossam Ahmed</h1>
                    <p className='fw-border fs-3 py-3' >And I'm a <span  className='fw-bold' ref={el} /></p>
        
                <div className='py-2 d-flex justify-align-content-around'>


                    <a data-aos="fade-right" data-aos-duration="3000" className='text-white fw-bold'  href="https://drive.google.com/file/d/1VhAAbUTdNRvyZqmHzNT3sRfSyF2TvUhu/view?usp=drive_link"> Download CV</a>
                    <a data-aos="fade-left" data-aos-duration="3000" className='text-white mx-3 fw-bold ' href="#">let's talk</a>

                    </div>
                
                </div>
            </div>
        </div>
    </div>
    </div>

</div>



</>
}
