
import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';

export default function Contact() {

  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_1n1egj8', 'template_4hgwgi4', form.current, 'lo7IRnu9CIHVAiURS')
      .then((result) => {
          console.log(result.text);
          if(result.text == "OK"){
            alert("   ")
          }
      }, (error) => {
          console.log(error.text);
      });
  };

  return <>
  <div className="vh-100 " id='contact'>
    <div className="container-fluid">
    <div className="title py-5  position-relative d-flex justify-content-center align-items-center wow fadeInDown">
            <h2 className="ab1 position-absolute text-uppercase fw-bolde fs-1" >contact me</h2>
            <h3 className="position-absolute text-uppercase fw-bolde text-white">contact me</h3>
        </div>
      <div className="container">
        <div className="row">


        <form className='py-5' ref={form} onSubmit={sendEmail}>
      <div className='py-3'>
      <label>Name</label>
      <input id='val' className='form-control' type="text" name="user_name" />
      </div>
      <div className='py-3'>
      <label>Email</label>
      <input id='val' className='form-control' type="email" name="user_email" />
      </div>
      <div className='py-3'>
      <label>Message</label>
      <textarea id='val' className='form-control' name="message" />
      </div>
      <div className='py-5'>
      <input className='btn  text-white d-block send m-auto w-50'  type="submit" value="Send" />
      </div>
    </form>


        </div>
      </div>
    </div>
  </div>
  </>
}
