
import { Icon } from '@iconify/react';
import { useState } from 'react';

export default function Nav() {







const [activeNav, setActiveNav] = useState('#');
return <>

<nav>
        <a aria-current="page" href="#home" onClick={() => setActiveNav('#home')} className={activeNav === '#home' ? 'active-link' : ''}><Icon className='icon' icon="material-symbols:home-outline" /></a>
        <a aria-current="page" href="#about" onClick={() => setActiveNav('#about')} className={activeNav === '#about' ? 'active-link' : ''}><Icon className='icon'  icon="carbon:user-profile" /></a>
        <a aria-current="page" href="#skills" onClick={() => setActiveNav('#skills')} className={activeNav === '#skills' ? 'active-link' : ''}><Icon className='icon' icon="pajamas:project" /></a>
        <a aria-current="page" href="#Experians" onClick={() => setActiveNav('#Experians')} className={activeNav === '#Experians' ? 'active-link' : ''}><Icon className='icon' icon="ant-design:code-outlined" /></a>
        <a aria-current="page" href="#project" onClick={() => setActiveNav('#project')} className={activeNav === '#project' ? 'active-link' : ''}><Icon className='icon' icon="mdi:briefcase-outline" /></a>
        <a aria-current="page" href="#contact" onClick={() => setActiveNav('#contact')} className={activeNav === '#contact' ? 'active-link' : ''}><Icon className='icon' icon="fa6-regular:message" /></a>
</nav>


</>
}
